* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  li {
    list-style: none;
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  body {
    color: #333;
    line-height: 1;
  }
  
  .main-visual{
    height: 100vh;
    background-color: lightsalmon;
    display: flex;
    align-items: center;
    justify-items: center;
  }

  .header {
    height: 100px;
  }

  .header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }


  .header .nav .gnb {
    display: flex;
    gap: 24px;
  }

  .nav-open {
    font-size: 24px;
    position: relative;
    z-index: 9;
  }

  .all-nav {
    width: 50%;
    height: 100vh;
    position: fixed;
    right: -100%;
    top: 0;
    background-color:beige;
    color: #333;
  }
